
"use client";

import type { Dispatch, ReactNode, SetStateAction } from 'react';
import { createContext, useContext, useEffect, useState } from 'react';

type Theme = "light" | "dark" | "system";

interface ThemeContextProps {
  theme: Theme;
  setTheme: Dispatch<SetStateAction<Theme>>;
  resolvedTheme: "light" | "dark";
}

const ThemeContext = createContext<ThemeContextProps | undefined>(undefined);

export function ThemeProvider({ children }: { children: ReactNode }) {
  const [theme, setTheme] = useState<Theme>(() => {
    if (typeof window !== 'undefined') {
      const storedTheme = localStorage.getItem("swiftcart-theme") as Theme | null;
      if (storedTheme) {
        return storedTheme;
      }
      return window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light";
    }
    return "light"; // Default for server rendering
  });

  const [resolvedTheme, setResolvedTheme] = useState<"light" | "dark">("light");

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const root = window.document.documentElement;
      let currentTheme: "light" | "dark";

      if (theme === "system") {
        currentTheme = window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light";
      } else {
        currentTheme = theme;
      }

      root.classList.remove("light", "dark");
      root.classList.add(currentTheme);
      localStorage.setItem("swiftcart-theme", theme);
      setResolvedTheme(currentTheme);
    }
  }, [theme]);

  useEffect(() => {
    // Ensure resolvedTheme is set on initial mount if theme is system
    if (typeof window !== 'undefined' && theme === 'system') {
       const currentTheme = window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light";
       setResolvedTheme(currentTheme);
    } else if (theme !== 'system') {
       setResolvedTheme(theme);
    }
  }, []);


  return (
    <ThemeContext.Provider value={{ theme, setTheme, resolvedTheme }}>
      {children}
    </ThemeContext.Provider>
  );
}

export function useTheme() {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error("useTheme must be used within a ThemeProvider");
  }
  return context;
}
