
"use client";

import type { Dispatch, ReactNode, SetStateAction} from 'react';
import { createContext, useContext, useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';

interface User {
  id: string;
  name: string;
  email?: string; // Or phone
}

interface AuthContextProps {
  isAuthenticated: boolean;
  user: User | null;
  login: (userData: User, otp: string) => Promise<void>; // otp for future use
  logout: () => void;
  isLoading: boolean;
  setIsLoading: Dispatch<SetStateAction<boolean>>;
}

const AuthContext = createContext<AuthContextProps | undefined>(undefined);

const AUTH_COOKIE_NAME = "swiftcart-auth-token";

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(true); // Check auth status on mount
  const router = useRouter();

  useEffect(() => {
    // Check for auth token in cookie on initial load
    const token = typeof window !== 'undefined' ? document.cookie.split('; ').find(row => row.startsWith(`${AUTH_COOKIE_NAME}=`)) : null;
    if (token) {
      // In a real app, verify token with backend and fetch user data
      // For simulation, assume token means authenticated
      const dummyUser: User = { id: "1", name: "Demo User", email: "user@example.com" };
      setUser(dummyUser);
      setIsAuthenticated(true);
    }
    setIsLoading(false);
  }, []);

  const login = async (userData: User, _otp: string) => {
    // Simulate API call and OTP verification
    setIsLoading(true);
    await new Promise(resolve => setTimeout(resolve, 1000)); // Simulate network delay
    
    // Simulate successful login
    setUser(userData);
    setIsAuthenticated(true);
    // Set a dummy auth token cookie
    if (typeof window !== 'undefined') {
      document.cookie = `${AUTH_COOKIE_NAME}=dummy-token; path=/; max-age=${60 * 60 * 24 * 7}`; // 7 days
    }
    setIsLoading(false);
    router.push('/dashboard');
  };

  const logout = () => {
    setIsLoading(true);
    setUser(null);
    setIsAuthenticated(false);
    // Remove the auth token cookie
    if (typeof window !== 'undefined') {
      document.cookie = `${AUTH_COOKIE_NAME}=; path=/; expires=Thu, 01 Jan 1970 00:00:00 GMT`;
    }
    setIsLoading(false);
    router.push('/login');
  };

  return (
    <AuthContext.Provider value={{ isAuthenticated, user, login, logout, isLoading, setIsLoading }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
}
